
import yheader from '../components/ycheader'
import Flexbox from 'vux/src/components/flexbox/flexbox.vue'
import FlexboxItem from 'vux/src/components/flexbox/flexbox-item.vue'

export default {
name: 'caifuFund',
data() {
return {
demandInfo:{
InvestAmount:0,
amount:0,
demandAgainst:0,
thousandReturns:0,
unSubmitAmount:0,
yestardayAgainst:0
}
}
},
computed: {

},
components: {
yheader,
Flexbox,
FlexboxItem
},
mounted() {
this.getCaifund();
},
methods: {
getCaifund(){
let _this = this;
this.post({
url: "/fundDemand/fundDemandInfo/v1.0",
success: function(e) {
if (e.code == "0000") {
_this.demandInfo=e.result.demandInfo;
}
}
})
}
}
}



